<template>
	<view class=global-view>
		<view class="top-button-box">
			<button ref="recommended" id="recommended" @click="switchPageState('recommended')"
				:disabled="isDisabledRecommended">推荐</button>
			<button ref="following" id="following" @click="switchPageState('following')"
				:disabled="isDisabledFollowing">关注</button>
		</view>
		<view class="container" id="homepage-container">
			<view class="article-box" v-for="item of articles" :key="item.aid" @click="navigateToDetails(item.aid)">
				<img src="/static/svg/default-cover.svg" alt="文章封面图" class="article-cover">
				<view class="article-info">
					<h1 class="title">{{item.title}}</h1>
					<p class="author">作者：{{item.author}}</p>
					<p class="tabloid">简介：{{item.tabloid}}</p>
				</view>
				<img src="/static/svg/arrow.svg" alt="" class="right-arrow">
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				pageState: "recommended",
				isDisabledRecommended: true,
				isDisabledFollowing: false,
				articles: [{
						aid: 1,
						title: "假字标题1",
						author: "Author1",
						tabloid: "横批无丁点，白云深处有人家。此地一为别，孤蓬万里征。绿海无波平，红尘里作琴瑟声..."
					},
					{
						aid: 2,
						title: "假字标题2",
						author: "Author2",
						tabloid: "风轻云淡日高天，江水流长草木繁。山川异域风月同天，文字飞扬思绵绵..."
					},
					{
						aid: 3,
						title: "假字标题3",
						author: "Author3",
						tabloid: "日出江花红胜火，春来江水绿如蓝。笔下生辉意境展，心中有事咏风寒..."
					},
					{
						aid: 4,
						title: "假字标题4",
						author: "Author4",
						tabloid: "青天有月来几时，我今停杯一问之。文字之舞风情万种，笔墨之间情深蜜..."
					},
					{
						aid: 5,
						title: "假字标题5",
						author: "Author5",
						tabloid: "山重水复疑无路，柳暗花明又一村。文字如花美眷侣，纸上得来终觉浅..."
					},
					{
						aid: 6,
						title: "假字标题6",
						author: "Author6",
						tabloid: "落日余晖映江面，千里同风几时还。情字如泉源源涌，篇章动人心弦扬..."
					},
					{
						aid: 7,
						title: "假字标题7",
						author: "Author7",
						tabloid: "春风得意马蹄疾，一日看尽长安花。文字千秋留痕迹，红颜白首情长在..."
					},
					{
						aid: 8,
						title: "假字标题8",
						author: "Author8",
						tabloid: "大漠孤烟直长河，独行寻梦去天涯。文章如刀切中要，锋芒毕露无人压..."
					},
					{
						aid: 9,
						title: "假字标题9",
						author: "Author9",
						tabloid: "天地英雄气，千秋尚自高。文字如山立，情感如水流..."
					},
					{
						aid: 10,
						title: "假字标题10",
						author: "Author10",
						tabloid: "风华是一指流砂，苍老是一段年华。文辞锋利似宝剑，每句都能戳心窝..."
					},
					{
						aid: 11,
						title: "假字标题11",
						author: "Author11",
						tabloid: "长亭外，古道边，芳草碧连天。文字有灵魂，流传的是真情..."
					},
					{
						aid: 12,
						title: "假字标题12",
						author: "Author12",
						tabloid: "青天有月来几时，我今停杯一问之。文章千古如长河，历历每篇都动情..."
					},
					{
						aid: 13,
						title: "假字标题13",
						author: "Author13",
						tabloid: "春花秋月何时了，往事知多少。文章如此多娇，引无数英雄竞折腰..."
					},
					{
						aid: 14,
						title: "假字标题14",
						author: "Author14",
						tabloid: "白日依山尽，黄河入海流。文章有情感，笔墨魂飘摇..."
					},
					{
						aid: 15,
						title: "假字标题15",
						author: "Author15",
						tabloid: "春风十里，不如你。文章里的你，如画中的风景..."
					},
					{
						aid: 16,
						title: "假字标题16",
						author: "Author16",
						tabloid: "此情可待成追忆，只是当时已惘然。文字里的她，如梦中的仙子..."
					},
					{
						aid: 17,
						title: "假字标题17",
						author: "Author17",
						tabloid: "曾经沧海难为水，除却巫山不是云。文字里的他，如诗中的英雄..."
					},
					{
						aid: 18,
						title: "假字标题18",
						author: "Author18",
						tabloid: "天长路远魂飞苦，梦魂不到关山难。文字是桥，连着心与心..."
					},
					{
						aid: 19,
						title: "假字标题19",
						author: "Author19",
						tabloid: "曲终人不见，江上数峰青。文章如梦，每字都是真情流露..."
					},
					{
						aid: 20,
						title: "假字标题20",
						author: "Author20",
						tabloid: "花自飘零水自流，一种相思两处愁。文字如此，情深似海不可求..."
					}
				]
			}
		},
		methods: {
			switchPageState(target) {
				if (target === "recommended") {
					this.isDisabledFollowing = false;
					this.isDisabledRecommended = true;
					this.getRecommendedArticle();
				} else if (target === "following") {
					this.isDisabledFollowing = true;
					this.isDisabledRecommended = false;
					this.getFollowingArticle();
				}
			},
			getRecommendedArticle() {},
			getFollowingArticle() {},
			navigateToDetails(aid) {
				uni.navigateTo({
					url: `/pages/article/article?aid=${aid}`
				})
			}
		},
		onLoad() {},
		onPullDownRefresh() {
			let reloadConditionally = (condition) => {}; // 根据选中项重新加载页面
			if (this.isDisabledRecommended)
				reloadConditionally("recommended");
			else if (this.isDisabledFollowing)
				reloadConditionally("following");
			setTimeout(uni.stopPullDownRefresh, 1000);
		}
	}
</script>

<style>
	@import url(/static/css/global-ui.css)
</style>